header {
  background-color: black;
  min-height: 100vh;
  display: flex;
  justify-content: center; /* horizontal center */
  align-items: center;
}

header .wrapper {
  display: flex;
  justify-content: space-between;
  align-items: center;
  width: 100%;
  height: 100%;
}

header .description {
  padding: 20px 20px;
  width: 650px;
  height: 450px;
  background: url(../../assets/close-up-green-leaf_1048944-27037486.jpg);
}

header .description .my-details {
  text-align: center;
  line-height: 1.6;
  padding: 10px 10px;
}

header .skills-wrapper {
  padding: 20px 20px;
  display: flex;
  flex-direction: column;
  width: 650px;
  height: 450px;
}

header .skills {
  width: 450px;
  background: black;
  display: flex;
  flex-wrap: wrap;
  gap: 10px;
}

.tag {
  padding: 6px 6px;
  font-size: 14px;
  font-weight: 500;
  color: #333;
  background: #d7f9e4; /* light blue background */
  border: 3px solid #0c9248;
  border-radius: 20px; /* pill shape */
  transition: all 0.3s ease;
  cursor: default;
  max-height: 14px;
}

h2 {
  color: white;
}
