#projects {
  height: 100vh;
  background-color: #dafec7;
  display: flex;
  justify-content: center;
  align-items: center;
}

#projects .wrapper {
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
  align-items: center;
  gap: 40px;
}

#projects .wrapper .card {
  width: 400px;
  height: 350px;
  background-color: white;
  display: flex;
  flex-direction: column;
}
#projects .card .image {
  height: 70%;
}
img {
  width: 100%;
  height: 100%;
}

#projects .card .description {
  border: 1px solid black;
  height: 35%;
  background-color: black;
  color: white;
  padding: 0 10px;
  text-align: center;
}
